    <hr align=center>
    <v-container fluid>
      <v-row>
        <v-col
          cols="12"
          md="3"
        >
          <v-card
            class="pa-3"
            outlined
            tile
          >
            <v-select
              v-model="space_selected"
              :items="space"
              label="Space"
              dense
            ></v-select>
            <v-layout wrap row>
              <v-flex xs12 sm6 md6 class="pb-2">
                <v-card color=cardColor>
                  <v-container pa-1>
                    <v-layout row>                   
                      <v-card-title primary-title>
                        <div>
                          <div class="grey--text" id="center">Safety Score</div>
                          <h3 class="headline" id="right">{{safetyScore}} /10</h3>
                        </div>
                        </v-card-title>
                    </v-layout>
                    <v-divider light></v-divider>
                    <v-card-actions class="pa-2">
                      <span class="grey--text caption font-italic">&nbsp;This Week</span>
                      <v-spacer></v-spacer>
                    </v-card-actions>
                  </v-container>
                </v-card>
              </v-flex>
              <v-flex xs12 sm6 md6 class="pb-2">
                <v-card>
                  <v-container pa-1>
                    <v-layout row>                   
                      <v-card-title primary-title>
                        <div>
                          <div class="grey--text" id="center">Safety Rate</div>
                          <h3 class="headline" id="right">{{safetyRate}} %</h3>
                        </div>
                      </v-card-title>
                    </v-layout>
                    <v-divider light></v-divider>
                    <v-card-actions class="pa-2">
                      <span class="grey--text caption font-italic">&nbsp;vs. Last Week</span>
                      <v-spacer></v-spacer>
                    </v-card-actions>
                  </v-container>
                </v-card>
              </v-flex>
              <v-flex>
              <daily-chart :chart-data="dailyDatacollection" width="350" height="300"></daily-chart>
              </v-flex>
              <v-flex xs12 sm6 md6>
                  <v-card color=cardColor>
                    <v-container pa-1>
                      <v-layout row>                   
                        <v-card-title primary-title>
                          <div>
                            <div class="grey--text" id="center">평균 인원</div>
                            <h3 class="headline" id="right">{{avg_people}} 명</h3>
                          </div>
                          </v-card-title>
                      </v-layout>                     
                    </v-container>
                  </v-card>
                </v-flex>
                <v-flex xs12 sm6 md6>
                  <v-card>
                    <v-container pa-1>
                      <v-layout row>                   
                        <v-card-title primary-title>
                          <div>
                            <div class="grey--text" id="center">최대 인원</div>
                            <h3 class="headline" id="right">{{max_people}} 명</h3>
                          </div>
                          </v-card-title>
                      </v-layout>
                    </v-container>
                  </v-card>
                </v-flex>
            </v-layout>
          </v-card>
        </v-col>
        <v-col cols="12" md="9">
          <v-card
            class="pa-3"
            outlined
            tile
          ><!--*********date picker for selecting a period*********-->
            <v-menu
              ref="menu"
              v-model="menu"
              :close-on-content-click="false"
              :return-value.sync="date"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="dateRangeText"
                  label="Date Picker"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker v-model="dates" range no-title scrollable>
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                <v-btn text color="primary" @click="$refs.menu.save(date)">OK</v-btn>
              </v-date-picker>
            </v-menu>

            <h2 id="chart">혼잡도</h2>
            <congestion-chart :chart-data="congestionDatacollection" height="150"/>
            <h2 id="chart">위험도</h2>
            <risk-chart :chart-data="riskDatacollection" height="150"/>
            <v-data-table 
              dense 
              :headers="headers" 
              :items="places"
              :items-per-page="5"
              item-key="location" 
              class="elevation-1"
            >
            </v-data-table>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>


<script>
  import DailyChart from '@/components/DailyChart'
  import CongestionChart from '@/components/CongestionChart'
  import RiskChart from '@/components/RiskChart'
  //import statics from '@/components/Figures_UI_02'
  import axios from 'axios'
  import moment from 'moment'
  import vueMoment from 'vue-moment'


  export default {
    name: 'UI_02',
    components: {
      DailyChart,
      CongestionChart,
      RiskChart,
      //statics,
    },
    data() {
      return {
        result: '',

        // Location
        groups: ['Lounge', 'Cafe'],        
        groups_selected: ['Lounge', 'Cafe'],
        groups_num: [0, 1],
        space: ['Lounge', 'Cafe'],
        space_selected: 'Lounge',
        space_num: 0,

        // Safety Score
        safetyScore: '',
        cardColor: '',
        prevScore: '',
        safetyRate: '',
        
        // Date Picker
        dates: ['2020-10-27', '2020-10-21'], 
        menu: false,
        firstDate: '', //moment().format("YYYY-MM-DD"),
        lastDate: '', //moment().format("YYYY-MM-DD"),
        days: moment(),

        colors: [ '#4285f4', '#34a853', '#fbbc05', '#ea4335' ],

        // Daily Chart
        dailyDatacollection: [],
        max_people: '',
        avg_people: '',

        // Chart Choice
        choice: '',

        // Congestion Chart
        congestionLabel: [],
        congestionData_1: [],
        congestionData_2: [],
        congestionDatacollection: [], // instantiating datacollection with null
        
        // Risk Chart   
        riskLabel: [],
        riskData_1: [],
        riskData_2: [],
        riskDatacollection: [],
             
        // Figures
        location: [],
        appropriate_people: [],
        max_congestion: [],
        avg_congestion: [],
        max_risk: [],
        avg_risk: [],
        high_risk: [],
        alert: [],
        headers: [
        {
          text: '장소',
          align: 'start',
          sortable: false,
          value: 'location',
        },
        { text: '적정 인원 (명)', value: 'appropriate_people' },
        //{ text: '최대 혼잡도 (%)', value: 'fat' },
        { text: '평균 혼잡도 (%)', value: 'avg_congestion' },
        //{ text: '최대 위험도 (%)', value: 'fat' },
        { text: '평균 위험도 (%)', value: 'avg_risk' },
        //{ text: '심각단계 평균 지속 시간 (분)', value: 'high_risk' },
        { text: '경고 발생 횟수 (회)', value: 'alert' },
        ],
        places: []
        //places: [{location: 'Co-Working Space', appropriate_people: 9, avg_congestion: 31.583333333333332, avg_risk: 35.30555555555556, alert: 5},
        //{location: 'Test Video', appropriate_people: 9, avg_congestion: 26.583333333333332, avg_risk: 30.305555555555557, alert: 5}]
      }
    },
    computed: {
      dateRangeText () {
        if(this.dates[0] > this.dates[1])
          [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]] 
        return this.dates.join(' ~ ')
      },
    },
    created() {
      this.getData()
    },
    watch: {
      dates (val) {
        this.getData();
      },
      groups_selected (val) {
        console.log('groups_selceted: '+this.groups_selected)
        this.groups_num.length=0
        for(var i=0; i<this.groups_selected.length; i++) {
          this.groups_num.push(this.groups.indexOf(this.groups_selected[i]))
        }
        console.log('groups_num: '+this.groups_num)
        this.space=this.groups_selected
        
        this.fillSpace();

        this.updateSafetyScore();
        this.updateDaily();

        this.updateData();
        this.drawGraph();

        this.fillPlaces();
      },
      space_selected (val) {
        this.space_num = this.groups.indexOf(this.space_selected)
        this.updateSafetyScore()
        this.updateDaily()
      },
    },
    methods: {
      link_to_live(){
      this.$router.push("/video");
      },
      fillGroups() {
        this.groups.length=0
        for(var i=0; i<this.result.length; i++){
          this.groups.push(this.result[i].location)
        }
        console.log('fillgroups-location: '+ this.groups)
      },
      fillGroupsSelected() {
        this.groups_selected.length=0
        this.groups_num.length=0
        for(var i=0; i<this.result.length; i++){
          this.groups_selected.push(this.result[i].location)
          this.groups_num.push(i)
        }
        console.log('fillgroupsselected-location: '+ this.groups_selected)
      },
      fillSpace() {
        this.space_selected = this.groups_selected[0]
        this.space_num = this.groups.indexOf(this.space_selected)
        console.log('fillspace-location: '+ this.space_selected)
      },
      updateSafetyScore() {
        this.safetyScore = parseInt(this.result[this.space_num].today.safety_score)
        if(this.safety_score> 5){
          this.cardColor = this.colors[0]
        }
        else{
          this.cardColor = this.colors[3]
        }
        this.prevScore = parseInt(this.result[this.space_num].today.prev_safety_score)
        this.safetyRate = ((this.safetyScore/this.prevScore)*100)-100
      },
      updateDaily() { // Daily Chart Data
        console.log('updateDaily')
        this.dailyDatacollection.length = 0
        this.dailyDatacollection = {
          // Data for the y-axis of the chart
          labels: [ '0~2 Hour', '2~4 Hour','4~6 Hour', '6~8 Hour', '8~10 Hour', '10~12 Hour',
           '12~14 Hour', '14~16 Hour', '16~18 Hour', '18~20 Hour', '20~22 Hour', '22~24 Hour'],
          datasets: [
            {
              label: "people",
              backgroundColor: this.colors[this.space_num],
              data: [ this.result[this.space_num].today.n_people[0], this.result[this.space_num].today.n_people[1],
              this.result[this.space_num].today.n_people[2], this.result[this.space_num].today.n_people[3],
              this.result[this.space_num].today.n_people[4], this.result[this.space_num].today.n_people[5],
              this.result[this.space_num].today.n_people[6], this.result[this.space_num].today.n_people[7],
              this.result[this.space_num].today.n_people[8], this.result[this.space_num].today.n_people[9],
              this.result[this.space_num].today.n_people[10], this.result[this.space_num].today.n_people[11] ]
            }
          ]
        }
        this.max_people = this.result[this.space_num].today.max_people
        this.avg_people = this.result[this.space_num].today.avg_people
      },
      updateLabel() { // C&R Chart Label
        var diff=this.firstDate
        this.congestionLabel.length = 0
        this.riskLabel.length = 0
        console.log('updateLabel days: '+ this.days)
        console.log('updateLabel this.firstDate: ' + this.firstDate)

        for (var i=0; i<=this.days; i++) { //어떤 형식으로 더할 건지 고민,,
          console.log(i + ' diff date: '+diff.format('YYYY-MM-DD'))
          this.congestionLabel.push(diff.format('YYYY-MM-DD')+' 12am')          
          this.riskLabel.push(diff.format('YYYY-MM-DD')+' 12am')
          for (var j=0; j<539; j++){
            this.congestionLabel.push('')
            this.riskLabel.push('')
          }
          this.congestionLabel.push('9am')
          this.riskLabel.push('9am')
          for (var j=0; j<179; j++){
            this.congestionLabel.push('')
            this.riskLabel.push('')
          }
          this.congestionLabel.push('12pm')
          this.riskLabel.push('12pm')
          for (var j=0; j<179; j++){
            this.congestionLabel.push('')
            this.riskLabel.push('')
          }
          this.congestionLabel.push('3pm')
          this.riskLabel.push('3pm')
          for (var j=0; j<179; j++){
            this.congestionLabel.push('')
            this.riskLabel.push('')
          }
          this.congestionLabel.push('6pm')
          this.riskLabel.push('6pm')
          for (var j=0; j<179; j++){
            this.congestionLabel.push('')
            this.riskLabel.push('')
          }
          this.congestionLabel.push('9pm')
          this.riskLabel.push('9pm')
          for (var j=0; j<179; j++){
            this.congestionLabel.push('')
            this.riskLabel.push('')
          }
          diff = diff.add(1, 'days')
        }       
      },
      updateData() { // C&R Chart Data 
        this.congestionData_1.length = 0
        this.congestionData_2.length = 0 
        this.riskData_1.length = 0
        this.riskData_2.length = 0

        for (var i=0; i<this.groups_selected.length; i++){
          if(i==0){
            for(var j=0; j<(this.days+1)*1440; j++){
              this.congestionData_1.push(this.result[0].selected_period.hourly_congestion[j]) // 값 넣기
              this.riskData_1.push(this.result[0].selected_period.hourly_risk[j])                
            }       
          }
          else if(i==1){
            for(var j=0; j<(this.days+1)*24; j++){
              this.congestionData_2.push(this.result[1].selected_period.hourly_congestion[j])
              this.riskData_2.push(this.result[1].selected_period.hourly_risk[j])                    
            }
          }         
        }
      },
      drawGraph () { // Draw All Charts
        this.congestionDatacollection = {
          // Data for the y-axis of the chart
          labels: this.congestionLabel,
          datasets: [
            {
              label: this.groups_selected[0],
              borderColor: this.colors[0],          
              fill: false,
              borderWidth: 4,
              // Data for the x-axis of the chart
              data: this.congestionData_1
            },
            {
              label: this.groups_selected[1],
              borderColor: this.colors[1],          
              fill: false,
              borderWidth: 4,
              // Data for the x-axis of the chart
              data: this.congestionData_2
            }
          ]
        },
        this.riskDatacollection = {
          // Data for the y-axis of the chart
          labels: this.riskLabel,
          datasets: [
            {
              label: this.groups_selected[0],
              borderColor: this.colors[0],
              fill: false,
              borderWidth: 4,
              // Data for the x-axis of the chart
              data: this.riskData_1
            },
            {
              label: this.groups_selected[1],
              borderColor: this.colors[1],
              fill: false,
              borderWidth: 4,
              // Data for the x-axis of the chart
              data: this.riskData_2
            }
          ]
        }
      },
      fillPlaces(){
        console.log("fillPlaces_groups_selected: " + this.groups_selected)
        console.log("fillPlaces_groups_num: " + this.groups_num)
        this.places.length = 0
        // problem
        var place0  = {
          location: '0',
          appropriate_people: 0,
          avg_congestion: 0,
          avg_risk: 0,
          alert: 0,
        }
        var place1  = {
          location: '0',
          appropriate_people: 0,
          avg_congestion: 0,
          avg_risk: 0,
          alert: 0,
        }
        for(var i=0; i<this.groups_selected.length; i++){     
          if(i==0){
            place0.location = this.result[this.groups_num[i]].location
            place0.appropriate_people = this.result[this.groups_num[i]].proper_n_people 
            place0.avg_congestion = this.result[this.groups_num[i]].selected_period.avg_congestion
            place0.avg_risk = this.result[this.groups_num[i]].selected_period.avg_risk
            place0.alert = this.result[this.groups_num[i]].selected_period.alarm_count
            this.places.push(place0);
          }     
          else if(i==1){
            place1.location = this.result[this.groups_num[i]].location
            place1.appropriate_people = this.result[this.groups_num[i]].proper_n_people 
            place1.avg_congestion = this.result[this.groups_num[i]].selected_period.avg_congestion
            place1.avg_risk = this.result[this.groups_num[i]].selected_period.avg_risk
            place1.alert = this.result[this.groups_num[i]].selected_period.alarm_count
            this.places.push(place1);
          }
        }
        console.log("fillPlaces[0]: "+this.places[0].location)
        console.log("fillPlaces[1]: "+this.places[1].location)
      },
      /* fillFigures() {
        this.place.length=0
        this.appropriate_people.length=0
        //this.max_congestion.length=0
        this.avg_congestion.length=0
        //this.max_risk.length=0
        this.avg_risk.length=0
        this.high_risk.length=0
        this.alert.length=0
        for( var i=0; i<this.groups_selected.length; i++){
          this.place.push(this.result[i].location)
          this.appropriate_people.push(this.result[i].proper_n_people)
          //this.max_congestion.push(this.result[i].max_congestion)
          this.avg_congestion.push(this.result[i].selected_period.avg_congestion)
          //this.max_risk.push(this.result[i].max_risk)
          this.avg_risk.push(this.result[i].selected_period.avg_risk)
          this.high_risk.push(this.result[i].selected_period.avg_high_risk_time)
          this.alert.push(this.result[i].selected_period.alert_count)
        }
        console.log('fillFigures - places: ' + this.places)
      },*/
      getData() {
        this.firstDate = moment(this.dates[0], 'YYYY-MM-DD')
        this.lastDate = moment(this.dates[1], 'YYYY-MM-DD')
        this.days = this.lastDate.diff(this.firstDate, 'days')
        console.log("getData - lastDate: " + this.lastDate.format('YYYY-MM-DD'))
        this.date = this.firstDate.format('YYYY-MM-DD') +'/'+ this.lastDate.format('YYYY-MM-DD')+'/hour'
        axios.get('http://115.145.212.100:53344/api/stats/'+ this.date)
          .then(res => {
            this.result = res.data;
            console.log(this.result)
            this.updateLabel();

            this.fillGroups();
            this.fillGroupsSelected();
            this.fillSpace();

            this.updateSafetyScore();
            this.updateDaily();

            this.updateData();
            this.drawGraph();

            this.fillPlaces();            
          })
      },
    }
  }
</script>

